a:hover, a:focus { color: #BA383D; outline: medium none; text-decoration: none }

::selection {
    color: #fff;
    background: #EB5055;
}
img { vertical-align: middle; max-width: 100%; height:auto; width: 100%; }
ul { list-style: none; }

.flex-caption { 
    background: transparent;
    color: #FFF;
    font-size: 2.7em;
    font-size: 2vw;
    font-weight: bold;
    left: 5%;
    margin-bottom: 0;
    max-width: 50%;  
    padding: 20px; 
    position: absolute; 
    text-transform: uppercase;
    top: 35%;
    z-index: 500;
    text-shadow: 3px 2px 0 rgba(0, 0, 0, 0.3)
}

@media only screen and (min-width: 1000px) {
    .flex-caption {
        font-size: 2.7em;
    }
}

.flex-caption-shift {
    top: 45%;
}
.flex-caption-shift2 {
    top: 57%;
}

.flipped {    
    transform: scaleX(-1);
    filter: FlipH;
    -ms-filter: "FlipH";
}

.story-para {
    text-indent: 2em;
    text-align: justify;
}

.required2 {
    content:"* "; 
    color:#df4449;
    position: absolute; 
    margin-left: -20px;
    margin-top: 10px;
    font-size: medium;
}

p.first:first-letter {
    font-size: 200%;
    font-weight: bold;    
}

body {counter-reset: point 0;}
.page-section {
    position: relative;
}
.page-section:before {
    content: "0" counter(point);
    counter-increment: point 1;
    position: absolute;
    top: 79px;
    left: 30px;
    width: 40px;
    height: 24px;
    text-align: right;    
    transform: rotate(-90deg);
    font: 18px/24px Arial,FreeSans,sans-serif;
    color: #bcbcbc;    
    border-left: 2px solid #bcbcbc;
    letter-spacing: 0.9px;
    z-index: 50;
}

/* Direction Nav */
.flex-direction-nav a { background: url("../images/bg_direction_nav.png") no-repeat scroll 0 0 #303441;
  cursor: pointer;display: block;height: 47px;margin: -20px 0 0;opacity: 0;position: absolute;text-indent: -9999px; top: 50%;
  width: 44px;}
 .flex-direction-nav .flex-next { background-position: 100% 0;  }

.flexslider:hover .flex-next { opacity: 0.8;  }
.flexslider:hover .flex-prev { opacity: 0.8;  }

.flex-direction-nav .disabled { opacity: .3!important; filter:alpha(opacity=30); cursor: default; }

.flex-direction-nav a.flex-next:before, flex-direction-nav a.flex-prev:before {
  content: '';
}

.flexslider {
    border-width: 0
}

