/* ==========================================================
                Language selector
============================================================= */

/* http://html5.by/blog/flexbox/ */

.lang-box-root {
    position: relative;
    padding-left: 0.5em;
    padding-top: 4px;    
}

.lang-box {
    color: #e5595c;
    font-family: 'allerregular', 'Helvetica', 'Arial', 'sans-serif';
    font-weight: bold;
        
    width: 6em;

    height: 32px;
    border: 1px solid #757b89;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    padding: 0.4em;    

    display: table;    
    border-radius: 2px;
    transition: border-color .3s ease;         
}

.lang-box:hover, .lang-box-opened {
    border-color: #cbcbcd;    
}

.icon-wrap > span {
    margin: 0 0 0 0.5em;
}

.lang-arrow {    
    width:8px;
    height:8px;
        
    transition: all .3s ease;    
}
use.lang-arrow-path {    
    fill: #757b89;
}
.lang-box-opened use.lang-arrow-path, .lang-box:hover use.lang-arrow-path {    
    fill: #cbcbcd;
}

.lang-box-opened .lang-arrow
{
    transform: rotate(-180deg)
}

.icon-wrap {
    display: table-cell;
    vertical-align: middle;
    float: left;
}
.arrow-wrap {
    display: table-cell;
    vertical-align: middle;
    float: right;
}

.lang-list {
    width: 9em;

    color: #A3A7B9;
    font-family: 'allerregular', 'Helvetica', 'Arial', 'sans-serif';
    background-color: #505461;
    padding: 0.2em;
    border: 1px solid #cbcbcd;
    

    position: absolute;
    z-index: 9999;    
    display: none;
    margin-top: 1px; 
}

.lang-list  a, .lang-list  a:hover {
    color: #f5696c;
}

.lang-list > div {    
    vertical-align: middle;
    padding: 0.2em 0.2em 0.4em 0.4em;
}
.lang-list > div:hover {    
    background-color: #707481;
}

.lang-list > div  span {
    margin: 0 0 0 0.5em;    
}

.lang-flag {    
    height: 16px;
    width: 24px;

    fill: currentColor;
    padding-top: 0.2em;
}

/* ==========================================================
                Progress
============================================================= */

.ajax-loader {
    cursor: wait;
    background:#eeeedd url('../images/ajax-loader.gif') no-repeat center center;    
    filter: alpha(opacity=75);
    opacity: 0.75;
    position: absolute;
    top: 0;left: 0;right: 0;bottom: 0;    
    z-index: 10000;    
}

.recaptcha-wrap {
    position: relative;
    height: 76px;
    
    background:#222;    

    transform:scale(0.7);
    transform-origin: 0 0;
}

.recaptcha-wrap > div {
    position: absolute;
    
    bottom: 10px;
    right:0px;
    font-size:10px;
    color:#ccc;
}

.alert-danger { 
    background-color: #f28e7e;
    border-color: #eb7c71;   
}
.alert-success {    
    background-color: #afd098;
    border-color: #86b986;
}

